//封装一个网络请求函数
import axios from "axios"

export function req(config){
   const instance=axios.create({
       //基本配置
       //baseURL:"http://wthrcdn.etouch.cn",//weather
       //baseURL:"https://autumnfish.cn",//music
        // baseURL:"http://127.0.0.1:3000",   
        //使用时路径，打包时需要注释，不然导致
        //其他终端连接时跨域，即使加了响应头也没用
       
      
     
       timeout:6000,
        // method:"post"
   });

   //axios拦截请求处理
   instance.interceptors.request.use((config)=>{
       //console.log(config);

       //拦截config，经过一系列的处理后需要把config还回去
       return config

   },
   //拦截失败时执行的函数
   (err)=>{
      console.log(err)
   });

   //axios响应拦截处理
   instance.interceptors.response.use(
   (rel)=>{
       //console.log(rel);
       //通过成功响应的rel对象里拿到响应的数据data
       //console.log(rel.data);

      //同样，对响应结果进行一系列处理也要还回去
      //return rel;
      return rel.data;

   },
   (err)=>{
       console.log(err);
   }
   );


   return instance(config);
   //instance方法默认返回一个promise
   //相对于我们执行了instance方法，并得到 一个promise
}
