<template>
<!-- 浏览器自带滚动条在移动端使用时带有卡顿，因此
使用better-scroll封装一个 -->

<div class="wrapper" ref="wrapper">
<div class="content">
<slot></slot> 
</div>   
</div>  
</template>

<script>
import bscroll from "better-scroll"
export default {
    data:function(){
        return{
            scroll:null,
            
        }
      },
        props:{
          probetype:{
            type:Number,
            default:0
          }
        },
      mounted:function(){
     //该函数此组件创建完调用
     //this.scroll=new bscroll(document.querySelector(".wrap"),
     this.scroll=new bscroll(this.$refs.wrapper,
     //ref绑定在元素this.$refs.wrapper拿到的是元素
     //ref绑定在组件this.$refs.wrapper拿到的是组件对象
     {
       //probeType:2  //不能监听惯性滚动 ,
       //为3可以监听惯性滚动,0和1是不监听,
       //出于性能考虑，默认为0，不监听
       probeType:this.probetype,   
       click:true,
       //在滚动条管理元素内部的元素默认是不能
       //设置点击事件的，传入click为true才能
       pullUpLoad:true,
       //表示要监听上拉

     });
     //监听滚动条位置
     this.scroll.on("scroll",(position)=>{
          // console.log(position);
          //position,坐标对象,有横坐标和纵坐标
          this.$emit("scrolling",position)
       })
     //监听滚动条滚到底部，到底部时触发
     this.scroll.on("pullingUp",()=>{
    // console.log("上拉加载更多");
      this.$emit("pullupload")
    //setTimeout(()=>{
    //this.scroll.finishPullUp()
    //},2000)
     //上拉完成，写了这个才会监听下一次
      //监听是否滚动到底部，不然pullingup只监听一次
    })

   },
   methods:{
      //定义一个方法，来返回顶部
      backTo:function(x,y,time=300){
      //这里是es6，参数可以写默认值，不传就是用这个
       this.scroll.scrollTo(x,y,time)
        //betterscroll对象里有一个scrollTo方法，3个参数，
        //水平，垂直，时间
      },
      pullupend:function(){
        setTimeout(()=>{
         this.scroll.finishPullUp()
    },2000)
      },
      refresh:function(fun){
        this.scroll.refresh()
        //better-scroll对象的refresh方法，
        //会重新计算可滚动区域的高度，
        //由于图片加载缓慢问题，导致滚动区域的计算错误
        //我们需要等图片加载完重新计算
        fun&&fun()
      },
      gety:function(){
        return this.scroll.y;
      }
   },
}
</script>


<style  scoped>
/* scoped表示这里写的样式是局部样式，只对这个组件的元素生效 */
.wrapper{
  position: absolute;
  top: 40px;
  bottom: 57.2px;
  right: 0;
  left: 0;
   overflow: hidden;
}
/* height:570px; */
</style>

 