<template>
<div>
  <mt-swipe :auto="3000" :speed=600>
  <!-- <mt-swipe :auto="3000" speed="600"> -->
  <!--<mt-swipe-item>1</mt-swipe-item>
  <mt-swipe-item>2</mt-swipe-item>
  <mt-swipe-item>3</mt-swipe-item> -->
<mt-swipe-item v-for="(value,index) in imglist" :key="index">
<img :src="value" alt="" @load="load">
</mt-swipe-item>

</mt-swipe>
</div>  
</template>

<script>
export default {
    data:function(){
     return{
       hasload:false
     }
    },
    props:{
        imglist:{
            type:Array,
            default:()=>[]
        }
    },
    methods:{
      load:function(){
        //只需要计算一次
       if(!this.hasload)
       {
          this.$emit("imghasload");
          this.hasload=true
       }
      },
    }
}
</script>

<style>
.mint-swipe{
  height: 200px;
 
}
.mint-swipe img{
  height: 100%;
  width: 100%;
}
.mint-swipe-indicators{
    
    z-index:99
}
.mint-swipe-indicator{
  opacity: 1;
  background-color: rgb(187, 182, 182);
  width: 6px;
  height: 6px;
}
.mint-swipe-indicator .is-active{
  background-color: white;
}
</style>